import React, { Component } from 'react';
import uuid from 'uuid/v1';
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Card,
  CardBody,
} from 'reactstrap';
import classnames from 'classnames';
import AddPost from './add-post';
import './perfil-nav.scss';

class PerfilNav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: '1',
      text: '',
      posts:[
        {
          text: 'Lorem, ipsum dolor sit amet consectetur adipisicing elit. In quod veniam iusto ipsum id enim necessitatibus dolorem consectetur porro eaque alias, officiis suscipit, accusamus vitae modi doloremque excepturi, fugiat tempora.',
          date: '26/06/2019'
        },
        {
          text: 'Lorem, ipsum dolor sit amet consectetur adipisicing elit. In quod veniam iusto ipsum id enim necessitatibus dolorem consectetur porro eaque alias, officiis suscipit, accusamus vitae modi doloremque excepturi, fugiat tempora.',
          date: '26/06/2019'
        },
        {
          text: 'Lorem, ipsum dolor sit amet consectetur adipisicing elit. In quod veniam iusto ipsum id enim necessitatibus dolorem consectetur porro eaque alias, officiis suscipit, accusamus vitae modi doloremque excepturi, fugiat tempora.',
          date: '26/06/2019'
        }
      ],
    };
    this.onToggle = this.onToggle.bind(this);
    this.onChangeText = this.onChangeText.bind(this);
    this.addPost = this.addPost.bind(this);
  }

  onToggle(tab) {
    const { activeTab } = this.state;
    if (activeTab !== tab) {
      this.setState({ activeTab: tab });
    }
  }

  onChangeText(e) {
    this.setState({ text: e.target.value });
  }

  addPost() {
    const date = new Date();
    const { text, posts } = this.state;
    const newPost = {
      date,
      text,
    }
    //this.setState(()=> ({ posts: posts.push(newPost) }))
    posts.push(newPost);
    console.log(date.toLocaleDateString('es-ES', { year: 'numeric', month: 'numeric', day:'numeric' }));
  }


  render() {
    const {
      activeTab,
      text,
      posts,
    } = this.state;
    return (
      <div>
        <Nav tabs>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '1', nav_item: this.state.activeTab !== '1'  })}
              onClick={()=>{this.onToggle('1')}}
            >
              Muro
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '2', nav_item: this.state.activeTab !== '2'  })}
              onClick={()=>{this.onToggle('2')}}
            >
              Historia
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '3', nav_item: this.state.activeTab !== '3'  })}
              onClick={()=>{this.onToggle('3')}}
            >
              Amigos
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '4', nav_item: this.state.activeTab !== '4'  })}
              onClick={()=>{this.onToggle('4')}}
            >
              Deseos
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent activeTab={activeTab} >
          <TabPane tabId='1'>
            <Row>
              <Col sm="12">
                <AddPost
                  value={text}
                  onChange={this.onChangeText}
                  onClick={this.addPost} 
                />
                <div className="line" />
              </Col>
            </Row>
            <div className="line_br" />
            <Row>
              {
                posts.map((post) => (
                  <Col key={uuid()} sm="12">
                    <Card className="post_cont shadow">
                      <CardBody>
                        {post.date}
                      </CardBody>
                      <CardBody className="card_body">
                        <p>{post.text}</p>
                      </CardBody>
                    </Card>
                  </Col>
                ))
              }
            </Row>
          </TabPane>
          <TabPane tabId='2'>
            <Row>
              <Col sm="12">
                <h2>Tab 2</h2>
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId='3'>
            <Row>
              <Col sm="12">
                <h2>Tab 3</h2>
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId='4'>
            <Row>
              <Col sm="12">
                <h2>Tab 4</h2>
              </Col>
            </Row>
          </TabPane>
        </TabContent>
      </div>
    );
  }
};

export default PerfilNav;
